
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import applyFooter from 'components/apply/apply-footer.vue'
import {applyQuery, applyCreate, recall} from '@/api/apply-detail'
export default {
name: 'applySupplySign',
components: {Datetime, Group, AlertPlugin, XTextarea, XButton, applyFooter},
data() {
return {
applyStatus: "1",
params: {
fid: "",
rows: 5,//原因行数
supplyTime: "",
supplyType: "",
supplyStatus: "",
supplyNumber: "",
showDel: false,
hrAddClockDetailList: [],
supplyReason: ""
}
}
},
methods: {

addRecord(){
if (this.params.supplyTime === "") {
this.$vux.alert.show({
content: '请输入申请时间',
});
} else if (this.params.supplyType === "") {
this.$vux.alert.show({
content: '请输入申请类型',
});
} else if (this.params.supplyStatus === "") {
this.$vux.alert.show({
content: '请输入申请状态',
});
} else {
return this.params.hrAddClockDetailList.push({
hrApplyDate: this.params.supplyTime,
hrAddClockType: this.params.supplyType,
hrAddClockTypeDetail: this.params.supplyStatus,
});
}
},

deleteRecord(index){
this.params.hrAddClockDetailList.splice(index, 1);
},
save(){//保存到待提交
if (this.params.hrAddClockDetailList.length === 0) {
this.$vux.alert.show({
content: '请输入补打卡记录',
});
} else {
this.$router.push({
name: 'apply',
params: {
"hrWorkflowType": "待提交",
"hrWorkflowTypeDetail": this.supplyType,//补打卡小类型  上下班打卡
"hrStartTime": this.supplyTime,//只有一个开始时间
"hrWorkflowReason": this.supplyReason,//申请原因
"hrWorkflowNode": "1"
}
});

}
},
saveOrSubmit(stateFlag){//提交到待审批
if (this.params.hrAddClockDetailList.length === 0) {
this.$vux.alert.show({
content: '请输入补打卡记录',
});
} else {
if (stateFlag === 'save') {
this.params.fid = "待提交";
this.$vux.loading.show({text: '保存中'});
} else {
this.$vux.loading.show({text: '提交中'});
}

applyCreate({
"hrWorkflowId": this.params.hrWorkflowId,
"hrWorkflowType": "applySupplySign",
"hrWorkflowTypeDetail": this.params.supplyType,  //销假类型
"hrStartTime": this.params.supplyTime,         //开始时间
"hrWorkflowReason": this.params.supplyReason,   //申请原因
"hrWorkflowNode": this.params.fid,
"hrWorkflowNodeStatus": "1",
"hrAddClockDetailList": this.params.hrAddClockDetailList,
"hrClockApplyTime": this.params.supplyTime,
}).then(response => {
if (response.data && response.data.rows) {
this.$vux.loading.hide();
if (stateFlag === 'save') {
this.$vux.toast.show({text: '保存成功'});
} else {
this.$vux.toast.show({text: '提交成功'});
window.localStorage.setItem('applyStatus', "2");
}
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
} else {
this.$vux.loading.hide();
if (stateFlag === 'save') {
this.$vux.toast.show({text: '保存失败'});
} else {
this.$vux.toast.show({text: '提交失败'});
}
}
}).catch(error => {
this.$vux.loading.hide();
});
}
},
revoke(){
this.$vux.toast.show({text: '撤回中'});
recall({"hrWorkflowId": this.params.hrWorkflowId}).then(response => {
if (response.data && response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.show({text: '撤回成功'});
window.localStorage.setItem('applyStatus', "1");
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
}
}).catch(error => {
this.$vux.loading.hide();
});
}
},
mounted() {

},
created() {

if (this.$route.query.item) {
let detailsItem = this.$route.query.item;
this.params.hrWorkflowId = detailsItem.hrWorkflowId;
this.params.supplyType = detailsItem.hrWorkflowTypeDetail;
this.params.supplyTime = detailsItem.hrClockApplyTime;
this.params.supplyStatus = detailsItem.hrAddClockTypeDetail;
this.params.supplyReason = detailsItem.hrWorkflowReason;
this.params.hrAddClockDetailList = detailsItem.hrAddClockDetailList;
}
if (this.$route.query.applyStatus === '1') {
this.applyStatus = "1";
} else if (this.$route.query.applyStatus === '2') {
this.applyStatus = "2";
} else if (this.$route.query.applyStatus === '3') {
this.applyStatus = "3";
}
}
}



